/* Basic Reset and Fonts */
body, html {
    height: 100%; /* Ensures the minimum height of the page is the viewport height */
    margin: 0;
    padding: 0;
    font-family: "Roboto", sans-serif;
    background: linear-gradient(#141e30, #243b55) no-repeat center center fixed; /* Fixes background to not repeat and cover the entire page */
    -webkit-background-size: cover;
    -moz-background-size: cover;
    -o-background-size: cover;
    background-size: cover; /* Ensures background covers the entire page */
  }
  
  .container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: flex-start; /* Align content to the top */
    width: 100%; /* Full width */
    padding-top: 20px; /* Space from top */
    min-height: 100vh; /* Ensures container takes at least the height of the viewport */
  }
  
  h1 {
    color: white; /* h1 color */
    margin-top: 0; /* Align content to top */
  }
  
  .tab-button {
    background-color: #343a40; /* Lighter background for tabs for contrast */
    color: #f8f9fa; /* Light text color */
    border: none;
    padding: 10px 20px;
    margin: 5px;
    cursor: pointer;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .tab-button.active, .tab-button:hover {
    background-color: #495057; /* Slightly darker on hover/active for feedback */
  }
  
  .tab-content {
    background-color: #20222c; /* Dark background for content area */
    color: #f0f2fc; /* Light text for readability */
    padding: 20px;
    margin-top: 20px;
    border-radius: 10px;
    box-shadow: 0 2px 5px rgba(0,0,0,0.2); /* Shadow for depth */
  }

.tab-content {
  background-color: #20222c;
  color: #f0f2fc;
  padding: 20px;
  margin-top: 20px;
  border-radius: 10px;
  box-shadow: 0 2px 5px rgba(0,0,0,0.2);
}

/* Additional Styling for Tabbed Interface */
.tabbed-interface {
  width: 90%;
  padding: 20px;
  background-color: #fff;
  box-shadow: 0 2px 5px rgba(0,0,0,0.1);
  border-radius: 10px;
}

/* Styling for Table Selector Component (if applicable) */
.table-selector {
  background-color: #f6f8ff;
  padding: 10px;
  border-radius: 5px;
}

/* Adjustments for Responsiveness */
@media (max-width: 768px) {
  .tab-button {
    padding: 5px 10px;
    font-size: 12px;
  }
}
